.metric-container {
    padding: 10px;
}

.metric-card-container {
    display: flex;
    justify-content: center;
    column-gap: 30px;
    padding: 8px;
    width: 100%;
    overflow: auto;
}

.metric-card-contents-container {
    display: flex;
    column-gap: 20px;
    list-style-type: none;
    padding-left: 0px;
    width: fit-content;
}

.card-container {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 7px;
    width: 350px;
    height: 120px;
}

.card-title {
    font-size: 15px;
    text-decoration: none;
    pointer-events: none;
    cursor: default;
}

.card-metric {
    font-size: 18px;
    text-decoration: none;
    pointer-events: none;
    cursor: default;
}

.image-container {
    display: flex;
    flex-direction: row;
    /* align-items: flex-start; */
    align-items: center;
    height: fit-content;
}

.image {
    margin-right: -14px;
    z-index: 1;
    border-radius: 50%;
    border: solid 3px white;
}

@media (max-width: 1024px) {
    .card-container {
        width: 300px;
    }

    .card-title {
        font-size: 12px;
    }

    .card-metric {
        font-size: 15px;
    }
}

@media (max-width: 768px) {

    .metric-card-container {
        justify-content: unset;
    }

    .card-container {
        width: 275px;
    }

    .card-title {
        font-size: 12px;
    }

    .card-metric {
        font-size: 15px;
    }
}



::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: white;

}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 99px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}